<template>
  <b-container>
    <div id="app">
      <b-row class="py-4">
        <b-input-group>
          <template #prepend>
            <b-input-group-text>
              <b-icon-search />
            </b-input-group-text>
          </template>
          <b-form-input
            v-model="search_q"
            placeholder="КОД або НАЗВА"
          >
            <!-- {{ search }} -->
          </b-form-input>
          <b-input-group-append>
            <b-dropdown
              :text="currentCategory.title"
              variant="primary"
            >
              <b-dropdown-item
                role="button"
                @click="selectCategory(0)"
              >
                <b-icon-search class="mr-2" />Обладнання
              </b-dropdown-item>
              <b-dropdown-item
                role="button"
                @click="selectCategory(1)"
              >
                <b-icon-search class="mr-2" />Ел. ланцюги
              </b-dropdown-item>
              <b-dropdown-divider />
              <b-dropdown-item role="button">
                <b-icon-book class="mr-2" />Запобіжники
              </b-dropdown-item>
            </b-dropdown>
            <!-- <b-button variant="outline-success active">Button</b-button>
            <b-button variant="info">Button</b-button> -->
          </b-input-group-append>
        </b-input-group>
      </b-row>
      <ItemHeader
        v-if="showItemHeader"
        :id="currentItemId"
        :title="currentItemDesc"
      />
      <FuzzResult
        v-if="showResultsTable"
        @selected-item="selectedItemId"
      />
      <ItemView
        v-if="showItemView"
        :is-connector="isConnector"
        :is-location="isLoaction"
        @select-data-source="selectDataSource"
      />
      <OptionsSelector
        v-if="showSelector"
        :proc-data="currentDataSource"
        @select-option="selectOption"
      />
      <ItemImage
        v-if="showSingleImage"
        :suffix="currentItemSuffix"
        :source="singleImageSrc"
      />
      <ConnectorTable
        v-if="showConnectorTable"
        :application="application"
        :results="pinout"
      />
      <!-- <div/> -->
    </div>
  </b-container>
</template>

<script>
import FuzzResult from "./components/FuzzResult.vue";
import ItemHeader from "./components/ItemHeader.vue";
import ItemView from "./components/ItemView.vue";
import OptionsSelector from "./components/OptionsSelector.vue";
import ItemImage from "./components/ItemImage.vue";
import ConnectorTable from "./components/ConnectorTable.vue";

import partsList from "./assets/partsList.json";
import circuitsList from "./assets/circuitsList.json";
import nmList from "./assets/nm.json";
import connList from "./assets/nm_t.json";
import schList from "./assets/sch.json";
import Fuse from "fuse.js";
const fusejsOptions = { keys: ["id", "desc"], threshold: 0.2 };
const categoriesList = [
  { title: "Обладнання", data: partsList, conn: true, loc: true },
  { title: "Ел. ланцюги", data: circuitsList, conn: false, loc: false },
];

const dataSources = { nm: nmList, sch: schList };

export default {
  name: "App",
  components: {
    // HelloWorld,
    FuzzResult,
    ItemHeader,
    ItemView,
    OptionsSelector,
    ItemImage,
    ConnectorTable,
  },
  data: function () {
    return {
      search_q: "",
      currentCategory: categoriesList[0],
      currentItemId: "",
      application: "",
      opt: "",
      harness: "",
      purpose: "",
      pinout: [],
      currentItemDesc: "",
      currentItemSuffix: "sch",
      currentDataSource: {},
      showSelector: false,
      showItemHeader: false,
      showItemView: false,
      showResultsTable: true,
      isConnector: false,
      isLoaction: false,
      showSingleImage: false,
      showConnectorTable: false,
      singleImageSrc: "",
    };
  },
  watch: {
    search_q: function () {
      this.debouncedGetAnswer();
      // if (this.search === "") this.showResultsTable = false
      // console.log(this.search === "")
      // this.getAnswer()
    },
  },
  created: function () {
    this.debouncedGetAnswer = this.lodash.debounce(this.getAnswer, 500);
  },

  methods: {
    getAnswer: function () {
      console.log("getAnswer");
      this.clearView();
      // this.showItemView = false;
      // this.showSelector = false;
      // this.showItemHeader = false;
      this.showResultsTable = true;
      const s = new Fuse(this.currentCategory.data, fusejsOptions);
      this.$emit(
        "update",
        s.search(this.search_q).map((i) => {
          return i.item;
        })
      );
    },
    selectCategory: function (c) {
      // console.log("Current category = ", categoriesList[c]);
      this.clearView();
      this.currentItemId = "";
      this.currentItemDesc = "";
      this.search_q = "";
      this.currentCategory = categoriesList[c];
      this.isConnector = this.currentCategory.conn;
      this.isLoaction = this.currentCategory.loc;
    },
    selectedItemId: function (r) {
      // this.clearView();
      this.currentItemId = r.id;
      this.currentItemDesc = r.desc;
      this.showResultsTable = false;
      if (this.currentItemId !== "") {
        this.showItemHeader = true;
        // console.log(
        //   dataSources["nm"].filter((i) => {
        //     return i.id === this.currentItemId;
        //   }).length
        // );
        if (
          dataSources["nm"].filter((i) => {
            return i.id === this.currentItemId;
          }).length !== 0
        ) {
          this.isConnector = true;
        }
        //   this.isConnector = true;
        // if (
        //   dataSources["nm"].filter((i) => {
        //     return i.id === this.currentItemId;
        //   })[0] !== undefined
        // )
        //   this.isConnector = true;
        // if (
        //   "sub" in
        //   dataSources["nm"].filter((i) => {
        //     return i.id === this.currentItemId;
        //   })[0]
        // ) {
        //   this.isConnector = true;
        // }
        this.showItemView = true;
        this.showSelector = false;
      }
    },
    selectDataSource: function (d) {
      // this.clearView;
      console.log(d);
      this.currentDataSource = {};
      this.currentItemSuffix = d;
      // console.log(this.currentItemId);
      this.showSelector = true;
      this.showSingleImage = false;
      this.showConnectorTable = false;
      // console.log(this.currentItemId);
      console.log(this.currentDataSource);
      this.currentDataSource = dataSources[d].filter((i) => {
        return i.id === this.currentItemId;
      })[0]["sub"];
      // this.$emit("change-data-source", this.currentDataSource);

      console.log(this.currentDataSource);
    },

    selectOption: function (o) {
      this.showSingleImage = true;
      this.singleImageSrc = o;
      this.showConnectorTable = false;
      if (this.currentItemSuffix === "nm") {
        // console.log(this.currentItemSuffix);
        this.showConnectorTable = true;
        const r = connList.filter((i) => {
          return i.id === "T_" + o;
        })[0];
        // console.log(r);
        this.application = r.connector.application;
        this.harness = r.connector.harness_p;
        this.pinout = r.pinout;
        // console.log(
        //   connList.filter((i) => {
        //     return i.id === "T_" + o;
        //   })[0]
        // );
        // this.$emit(
        //   "connector",
        //   connList.filter((i) => {
        //     return i.id === "T_" + o;
        //   })[0]
        // );
      }
      // console.log(o);
    },
    clearView: function () {
      this.showResultsTable = false;
      this.showItemHeader = false;
      this.showItemView = false;
      this.showSelector = false;
      this.showSingleImage = false;
      this.showConnectorTable = false;
      this.isConnector = false;
      this.isLoaction = false;
    },
  },
};
</script>

<style>
.dropdown-item {
  font-size: smaller;
  /* max-height: 280px;
  overflow-y: auto; */
}
</style>
