<template>
  <b-row v-if="checkResult">
    <b-table
      bordered
      hover
      head-variant="dark"
      :fields="fields"
      :items="results"
      @row-clicked="selectRow"
    />
  </b-row>
</template>

<script>
export default {
  data: function () {
    return {
      // search: '',
      // showResultsTable: false,
      results: [],
      fields: [
        { key: "id", label: "Код", tdClass: "pointer table-col-id" },
        { key: "desc", label: "Найменування", tdClass: "pointer" },
      ],
    };
  },

  computed: {
    checkResult: function () {
      return !this.lodash.isEmpty(this.results);
    },
  },
  created: function () {
    this.$parent.$on("update", this.setResult);
  },
  methods: {
    setResult: function (r) {
      this.results = r;
    },
    selectRow: function (r) {
      console.log("ITEM Selected = ", r.id);
      this.$emit("selected-item", r);
      // this.clearView()
    },
  },
};
</script>

<style scoped>
.b-table >>> td {
  cursor: pointer;
  vertical-align: middle;
}

.b-table >>> .table-col-id {
  font-weight: bold;
}
</style>
